(function() {
  'use strict';

  angular
    .module('boardgame')
    .controller('TablesPageController', TablesPageController);

  /** @ngInject */
  function TablesPageController($scope, $log, clientServerCommFactory, $location) {
    console.log("tablesList ctrl ");

    var vm = this;
    vm.communication = {};
    vm.tables = [];
    vm.communication.collection = clientServerCommFactory.getCollection();
    vm.myTableId;
    vm.tableClosed = false;
    vm.tableBot = false;
    vm.tablePassword = '';


    vm.communication.sendMsg = function(msg){
      clientServerCommFactory.sendMsg(msg);
    }

    vm.communication.sendMsg({type: 3});

    vm.newTable = function(){
      console.log(vm.tableBot+" "+vm.tableClosed+" "+vm.tablePassword);

      vm.communication.sendMsg({type: 4, login:  clientServerCommFactory.getLogin(), bot: vm.tableBot,
        closed: vm.tableClosed, password: vm.tablePassword});
    }

    vm.appendToTable = function(tabId, password){
      console.log(password);
      vm.communication.sendMsg({type: 5, login:  clientServerCommFactory.getLogin(), tableId: tabId, password: password});
    }

    vm.leaveTable = function(tabId){
      vm.communication.sendMsg({type: 6, login:  clientServerCommFactory.getLogin(), tableId: tabId})
    }

    vm.onMessageAction = function(value){
      switch(value.type){
        case 3:
          if (value.result == 0){
            vm.tables = value.tables;
          } else{
          }
          break;

        case 4:
          if (value.result == 1){
            vm.tables = value.tables;
          } else if(value.result == 0){
            vm.tables = value.tables;
            //przejscie na str gry
            console.log(value.tables);
            clientServerCommFactory.myTableId = value.tables[value.tables.length-1].id;
            $location.url('/board/'+value.tables[value.tables.length-1].id);
          }
          break;

        case 5:
          console.log("case 5");
          console.log(value.result);

          if(value.result == 0){
            vm.tables = value.tables;
            //przejscie na str gry

            clientServerCommFactory.myTableId = value.tableId;
            $location.url('/board/'+value.tableId);

          } else if(value.result == 3){
            vm.tables = value.tables;
            console.log("ktos dolaczyl");
            console.log(vm.tables);

          } else if(value.result == -2){
            vm.tables = value.tables;
            vm.passError = 'Niepoprawne hasło!';
            console.log("ktos dolaczyl");
            console.log(vm.tables);

          }
          else {
            // stol juz zajety byl
            console.log("stol zajety");
          }
          break;
      }
    }

    $scope.$watchCollection(function(){
      return clientServerCommFactory.getCollection();
    }, function(value){
      if((angular.isUndefined(value))||(angular.isUndefined(value[value.length-1]))) return;
      vm.onMessageAction(value[value.length-1]);
      clientServerCommFactory.getCollection().splice(0,1);
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('boardgame')
    .controller('LoginPageController', LoginPageController);

  /** @ngInject */
  function LoginPageController($scope, clientServerCommFactory, accessFactory, $log, $location) {
    var vm = this;
    vm.communication = {};
    vm.collection = [];
    vm.myLogin = '';
    vm.errorLogin = '';

    vm.login = function(){
      getClientServerComm();
      vm.communication.sendMsg({type: 1, login: vm.myLogin});
      vm.communication.setLogin(vm.myLogin);
    }
    vm.getAccess = function(){
      accessFactory.getPermission();       //call the method in acccessFac to allow the user permission.
    }
    vm.about = function(){
      $location.url('/about');
    }
    vm.onMessageAction = function(value){
      if(value.type == 1){
          if (value.result == 0){
            vm.getAccess();
            $location.url('/tablesList');
          } else {
            vm.errorLogin = 'Taki nick już istnieje, podaj inny';
            //komm,zeby podac inny login
          }
      }
    }


    function getClientServerComm(){
      vm.communication.collection = clientServerCommFactory.getCollection();
      clientServerCommFactory.startCommunication();
      vm.communication.sendMsg = function(msg){
        clientServerCommFactory.sendMsg(msg);
      }
      vm.communication.setLogin = function(login){
        clientServerCommFactory.setLogin(login);
      }

      $scope.$watchCollection(function(){
        return clientServerCommFactory.getCollection();
      }, function(value){
        if((angular.isUndefined(value))||(angular.isUndefined(value[value.length-1]))) return;
        vm.onMessageAction(value[value.length-1]);
      }, true);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('boardgame')
    .controller('GamePageController', GamePageController);

  /** @ngInject */
  function GamePageController($scope, $log, clientServerCommFactory, $location, $routeParams, $window) {
    var vm = this;
    vm.communication = {};
    vm.tables = [];
    vm.myId = -1;
    vm.myMovement = false;
    vm.movementId = -1;
    vm.myLogin = '';
    vm.table = {};
    vm.myTableId = -1;
    vm.players = [];
    vm.isRunning = false;
    vm.btnRoll = false;
    vm.serverResponse = -1;
    vm.welcome = false;
    vm.availableArchitects = [];
    vm.availableFirms = [];
    vm.btnLeave = false;
    vm.communication.collection = clientServerCommFactory.getCollection();
    vm.communication.gameMessages = clientServerCommFactory.getGameMessages();

    vm.leaveTable = function(){
      console.log("leave");
      vm.btnLeave = true;
      vm.communication.sendMsg({type: 6, login:  vm.myLogin, tableId: clientServerCommFactory.myTableId, myId: vm.myId });
    };

    vm.rollDice = function(){
      console.log("rollDice");
      vm.btnRoll = false;///!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
      vm.communication.sendMsg({type: 8, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId});
    };

    vm.buyMaterialSendServer = function(buyList, sum, shopId){
      console.log("shopping");
      vm.communication.sendMsg({type: 9, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId, buyList: buyList, sum: sum, shopId: shopId});
    };
    vm.buyPlotSendServer = function(plot, officeId){
      console.log("plot");
      vm.communication.sendMsg({type: 10, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId, plot: plot, officeId: officeId});
    };
    vm.buyProjectSendServer = function(architect){
      console.log("project");
      vm.communication.sendMsg({type: 11, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId, architect: architect});
    };
    vm.answerQuestionSendServer = function(good){
      console.log("question");
      vm.communication.sendMsg({type: 12, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId, good: good});
    };
    vm.penaltyAcceptSendServer = function(penalty){
      console.log("penalty");
      vm.communication.sendMsg({type: 13, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId, mandate: penalty});
    };

    vm.endMovementSendServer = function(firm){
      console.log("end move");
      vm.communication.sendMsg({type: 14, login:  vm.myLogin, tableId: vm.myTableId,
        myId: vm.myId, firm: firm});
    };


    vm.onMessageAction = function(value){
      switch(value.type){
        case 6:
          //leave
          if (value.result == 0){
            vm.communication.gameMessages = [];
            vm.communication = {};
            vm.tables = [];
            clientServerCommFactory.myTableId = -1;
            $location.url('/tablesList/');

          } else if(value.result == 1){
            vm.serverResponse = 6;
            vm.leaveServerMsg = value;
          }
          break;

        case 7:
          console.log("77777777777777777");
          console.log(value);

          if (value.result == 0){
            vm.myId = value.myId;
            vm.myMovement = value.myMovement;
            vm.movementId = value.movementId;
            vm.myLogin = value.table.players[value.myId].login;
            vm.table = value.table;
            vm.myTableId = value.table.id;
            vm.players = value.table.players;
            vm.isRunning = true;
            vm.welcome = true;
            if(vm.myMovement){
              vm.btnRoll = true;
            }

          }
          break;

        case 8:
          console.log("dice");
          console.log(value);
          vm.serverResponse = 8;
          vm.rollresponse = value;
          vm.welcome = false;

          break;

        case 9:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 9;
          vm.buyMaterialServerMsg = value;
          break;

        case 10:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 10;
          vm.buyPlotServerMsg = value;
          break;

        case 11:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 11;
          vm.buyProjectServerMsg = value;
          break;

        case 12:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 12;
          vm.answerQuestionServerMsg = value;
          break;

        case 13:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 13;
          vm.penaltyAcceptServerMsg = value;
          break;

        case 14:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 14;
          vm.endMovementServerMsg = value;
          vm.myMovement = value.myMovement;
          vm.movementId = value.next;
          vm.firm = value.firm;
          if(vm.myMovement){
            vm.btnRoll = true;
          }
          break;

        case 15:
          console.log("serverResponse");
          console.log(value);
          vm.serverResponse = 15;
          vm.winServerMsg = value;
          vm.myMovement = false;
          vm.movementId = -1;

          break;
      }
    };

    vm.communication.sendMsg = function(msg){
      clientServerCommFactory.sendMsg(msg);
    };
/*
    $window.onbeforeunload = function (e) {
      if(vm.btnLeave){
        vm.btnLeave = false;
        return;
      }
      vm.endMovementSendServer(-1);
      vm.communication.sendMsg({type: 6, login:  vm.myLogin, tableId: vm.myTableId, myId: vm.myId });
    };
*/
$scope.$watchCollection(function(){
  return clientServerCommFactory.getGameMessages();
}, function(value){
  if((angular.isUndefined(value))||(angular.isUndefined(value[value.length-1]))) return;
  vm.onMessageAction(value[value.length-1]);
}, true);

  }

})();

